import { useEffect } from "react"
import { deleteAudio } from "../../handlers/voice-recorder/recording-list"
import generateKey from "../../utils/generate-key"

export default function useRecordingsList(audio, blob, methods, records) {
  function setRecordings(records) {
    methods.recordsResave(records)
  }

  useEffect(() => {
    const { id = 'canvas1' } = document.querySelector('canvas')
    const canvasId = Number(id.replace('canvas', '')) || 1

    if (audio) {
      const recs = [
        ...records, 
        { key: generateKey(), audio, blob, canvasId, ...{ x: 20, y: 20, r: 10 } }
      ]
      setRecordings(recs)
    }
  }, [audio, blob])

  return {
    setRecordings,
    deleteAudio: (audioKey) => deleteAudio(audioKey, setRecordings, records),
  }
}