export function moveTop(arr, key, canvasId) {
  const others = arr
    .filter(item => item.canvasId !== canvasId)

  const filtered = arr
    .filter(item => item.canvasId === canvasId)

  const index = filtered
    .findIndex(item => item.key === key)

  // Can not move the first item
  if (index === 0) return arr;

  [
    filtered[index - 1],
    filtered[index]
  ] = [
    filtered[index],
    filtered[index - 1]
  ];

  return [...others, ...filtered]
}

export function moveDown(arr, key, canvasId) {
  const others = arr
    .filter(item => item.canvasId !== canvasId)

  const filtered = arr
    .filter(item => item.canvasId === canvasId)

  const index = filtered
    .findIndex(item => item.key === key)

  // Can not move the first item
  if ((index + 1) === filtered.length) return arr;

  [
    filtered[index + 1],
    filtered[index]
  ] = [
    filtered[index],
    filtered[index + 1]
  ];

  return [...others, ...filtered]
}