import React from "react";

/*
  Functional of figures
*/

export class _Figures extends React.Component {
  // Coordinates is for the moving and resize for the `resaveItem` method
  adapSelectingArea(figureName, x1, y1, x2, y2) {
    switch(figureName) {
      case 'triangle':
        return { x1, y1, x2: x2 + (x2 - x1), y2 }
      case 'arbitraryTriangle':
        return {x1, y1, x2: x2 + (x2 - x1) * 3, y2}
      case 'rhombus':
        return {x1, y1: y1 - (y2 - y1), x2: x2 + (x2 - x1), y2}
      case 'parallelogram':
        if ([1, 4].includes(this.adapQuarter(x1, y1, x2, y2, 14).quater)) {
          return {x1: x1 - 40, y1,  x2: x2 + 40, y2}
        } else {
          return {x1, y1, x2, y2}
        }
      case 'circle':
        return {x1: x2 - (x2 - x1), y1: y2 - (x2 - x1), x2: x2 + (x2 - x1), y2:  y2 + (x2 - x1)}
      case 'ellipse':
        return {x1: x2 - (x2 - x1), y1: y2 - (y2 - y1), x2: x2 + (x2 - x1), y2: y2 + (y2 - y1)}
      case 'coordinatesSystem':
        return {x1: x2 - (x2 - x1), y1: y2 - (y2 - y1), x2: x2 + (x2 - x1), y2: y2 + (y2 - y1)}
      default: return {x1, y1, x2, y2}
    }
  }

  onMouseUpEvent(state, x1, y1, x2, y2, memoryResave) {
    let { instruments, rotationDegree } = state

    let resaveItem = selectedAreaCoordinates => {

      let rotatedCoordinates = this.getRotatedCoordinates(selectedAreaCoordinates, rotationDegree)

      return {
        active: true,
        type: instruments.figure.selected,
        types: instruments.figure.types,
        canvasId: state.activeCanvasId,
        color: state.globalColor,
        fillColor: instruments.figure.settings.fillColor,
        dashedMode: instruments.figure.settings.dashedMode,
        coordinates: {
          figure: {x1, y1, x2, y2},
          selectedArea: selectedAreaCoordinates,
          rotatedSelectedAreaCoordinates: rotatedCoordinates
        },
        rotationDegree: rotationDegree
      }
    }

    switch(true) {
      case instruments.figure.types.triangle:
        // Triangle 
        this.selectingArea(state, x1, y1, x2 + (x2 - x1), y2)
        memoryResave(resaveItem({ x1, y1, x2: x2 + (x2 - x1), y2 }))
        break
      case instruments.figure.types.rightTriangle:
        // Right Triangle
        this.selectingArea(state, x1, y1, x2, y2)
        memoryResave(resaveItem( {x1, y1, x2, y2} ))
        break
      case instruments.figure.types.arbitraryTriangle:
        // Arbitrary Triangle
        this.selectingArea(state, x1, y1, x2 + (x2 - x1) * 3, y2)
        memoryResave(resaveItem( {x1, y1, x2: x2 + (x2 - x1) * 3, y2} ))
        break
      case instruments.figure.types.rhombus:
        // Rhombus
        this.selectingArea(state, x1, y1 - (y2 - y1),  x2 + (x2 - x1), y2)
        memoryResave(resaveItem( {x1, y1: y1 - (y2 - y1), x2: x2 + (x2 - x1), y2} ))
        break
      case instruments.figure.types.rectangle:
        // Rectangle
        this.selectingArea(state, x1, y1, x2, y2)
        memoryResave(resaveItem( {x1, y1, x2, y2} ))
        break
      case instruments.figure.types.parallelogram:
        // Parallelogram
        if ([1, 4].includes(this.adapQuarter(x1, y1, x2, y2, 14).quater)) {
          this.selectingArea(state, x1 - 40, y1, x2 + 40, y2)
          memoryResave(resaveItem( {x1: x1 - 40, y1,  x2: x2 + 40, y2} ))
        } else {
          this.selectingArea(state, x1, y1, x2, y2)
          memoryResave(resaveItem( {x1, y1, x2, y2} ))
        }
        break
      case instruments.figure.types.circle:
        // Circle
        this.selectingArea(state, x2 - (x2 - x1), y2 - (x2 - x1), x2 + (x2 - x1), y2 + (x2 - x1))
        memoryResave(resaveItem( {x1: x2 - (x2 - x1), y1: y2 - (x2 - x1), x2: x2 + (x2 - x1), y2:  y2 + (x2 - x1)} ))
        break
      case instruments.figure.types.ellipse:
        // Ellipse
        this.selectingArea(state, x2 - (x2 - x1), y2 - (y2 - y1), x2 + (x2 - x1), y2 + (y2 - y1))
        memoryResave(resaveItem( {x1: x2 - (x2 - x1), y1: y2 - (y2 - y1), x2: x2 + (x2 - x1), y2: y2 + (y2 - y1)} ))
        break
      case instruments.figure.types.coordinatesSystem:
        // Coordinates System
        this.selectingArea(state, x2 - (x2 - x1), y2 - (y2 - y1), x2 + (x2 - x1), y2 + (y2 - y1))
        memoryResave(resaveItem( {x1: x2 - (x2 - x1), y1: y2 - (y2 - y1), x2: x2 + (x2 - x1), y2: y2 + (y2 - y1)} ))
        break
      default:;
    }
  }

  drawFigureSelectArea(figure, state, ctx) {
    let { x1, y1, x2, y2 } = figure.coordinates.selectedArea

    this.selectingArea(state, x1, y1, x2, y2)
  }

  setStyles(state) {
    let { ctx, instruments, globalColor } = state
    
    // Common border style
    ctx.strokeStyle = globalColor
    ctx.lineJoin = 'round'
    ctx.lineCap = 'round'
    ctx.lineWidth = 1
    instruments.figure.settings.dashedMode ?  ctx.setLineDash([5, 15]) :  ctx.setLineDash([])

    const { fillColor } = instruments.figure.settings

    if (fillColor) {
      if (!instruments.figure.types.coordinatesSystem) {
        ctx.fillStyle = fillColor
        ctx.fill()
      }
    }
  }
  
  drawFigure(state, x, y, x2, y2) {
    let { instruments } = state

    switch(true) {
      case instruments.figure.types.triangle:
        // Triangle 
        this.triangle(state, x, y, x2, y2)
        break
      case instruments.figure.types.arbitraryTriangle:
        // Arbitrary Triangle
        this.arbitraryTriangle(state, x, y, x2, y2)
        break
      case instruments.figure.types.rightTriangle:
        // Right Triangle
        this.rightTriangle(state, x, y, x2, y2)
        break
      case instruments.figure.types.rhombus:
        // Rhombus
        this.rhombus(state, x, y, x2, y2)
        break
      case instruments.figure.types.rectangle:
        // Rectangle
        this.rectangle(state, x, y, x2, y2)
        break
      case instruments.figure.types.parallelogram:
        // Parallelogram
        this.parallelogram(state, x, y, x2, y2)
        break
      case instruments.figure.types.circle:
        // Circle
        this.circle(state, x, y, x2, y2)
        break
      case instruments.figure.types.ellipse:
        // Ellipse
        this.ellipse(state, instruments, x, y, x2, y2)
        break
      case instruments.figure.types.coordinatesSystem:
        // Coordinates System
        this.coordinatesSystem(state, x, y, x2, y2)
        break
      default:;
    }
  }

  selectingArea(state, x1, y1, x2, y2) {
    const { ctx } = state

    ctx.setLineDash([14, 15])
    ctx.strokeStyle = '#019267'

    // Adding padding of figure
    let w = 14
    let [n1, n2, n3, n4] = this.adapQuarter(x1, y1, x2, y2, w).numbers
    this.rectangle(state, x1 + n1, y1 + n2, x2 + n3, y2 + n4)
  }

  triangle(state, x1, y1, x2, y2) {
    const { ctx, rotationDegree: angle } = state
    
    const x0 = (x1 + x2 + (x2 - x1)) / 2
    const y0 = (y2 + y1) / 2

    const rotateMode = Boolean(angle)

    let x, y

    ctx.beginPath()

    x = x1; y = y1
    if (rotateMode) ({ x, y } = this.rotateCoordinates(x, y, x0, y0, angle))
    ctx.moveTo(x, y)

    x = x2; y = y2
    if (rotateMode) ({ x, y } = this.rotateCoordinates(x, y, x0, y0, angle))
    ctx.lineTo(x, y)

    x = x2 + (x2 - x1); y = y1
    if (rotateMode) ({ x, y } = this.rotateCoordinates(x, y, x0, y0, angle))
    ctx.lineTo(x, y)

    ctx.closePath()
    ctx.stroke()
  }

  arbitraryTriangle(state, x1, y1, x2, y2) {
    const { ctx, rotationDegree: angle } = state
    
    const x0 = (x1 + (x2 + (x2 - x1) * 3)) / 2
    const y0 = (y2 + y1) / 2

    const rotateMode = Boolean(angle)

    let x, y

    ctx.beginPath()

    x = x1; y = y1
    if (rotateMode) ({ x, y } = this.rotateCoordinates(x, y, x0, y0, angle))
    ctx.moveTo(x, y)

    x = x2; y = y2
    if (rotateMode) ({ x, y } = this.rotateCoordinates(x, y, x0, y0, angle))
    ctx.lineTo(x, y)

    x = x2 + (x2 - x1) * 3; y = y1
    if (rotateMode) ({ x, y } = this.rotateCoordinates(x, y, x0, y0, angle))
    ctx.lineTo(x, y)

    ctx.closePath()
    ctx.stroke()
  }

  rightTriangle(state, x1, y1, x2, y2) {
    const { ctx, rotationDegree: angle } = state
    
    const x0 = (x1 + x2) / 2
    const y0 = (y2 + y1) / 2

    const rotateMode = Boolean(angle)

    let x, y

    ctx.beginPath()

    x = x1; y = y1
    if (rotateMode) ({ x, y } = this.rotateCoordinates(x, y, x0, y0, angle))
    ctx.moveTo(x, y)

    x = x2; y = y1
    if (rotateMode) ({ x, y } = this.rotateCoordinates(x, y, x0, y0, angle))
    ctx.lineTo(x, y)

    x = x2; y = y2
    if (rotateMode) ({ x, y } = this.rotateCoordinates(x, y, x0, y0, angle))
    ctx.lineTo(x, y)

    ctx.closePath()
    ctx.stroke()
  }

  rhombus(state, x1, y1, x2, y2) {
    const { ctx, rotationDegree: angle } = state
    
    const x0 = x2
    const y0 = y1

    const rotateMode = Boolean(angle)

    let x, y

    ctx.beginPath()

    x = x1; y = y1
    if (rotateMode) ({ x, y } = this.rotateCoordinates(x, y, x0, y0, angle))
    ctx.moveTo(x, y)

    x = x2; y = y2
    if (rotateMode) ({ x, y } = this.rotateCoordinates(x, y, x0, y0, angle))
    ctx.lineTo(x, y)

    x = x2 + (x2 - x1); y = y1
    if (rotateMode) ({ x, y } = this.rotateCoordinates(x, y, x0, y0, angle))
    ctx.lineTo(x, y)
    
    x = x2; y = y1 - (y2 - y1)
    if (rotateMode) ({ x, y } = this.rotateCoordinates(x, y, x0, y0, angle))
    ctx.lineTo(x, y)

    ctx.closePath()
    ctx.stroke()
  }

  rectangle(state, x1, y1, x2, y2) {
    const { ctx, rotationDegree: angle } = state

    const x0 = (x2 + x1) / 2
    const y0 = (y2 + y1) / 2

    const rotateMode = Boolean(angle)

    let x, y

    ctx.beginPath()

    x = x1 
    y = y1
    if (rotateMode) ({ x, y } = this.rotateCoordinates(x, y, x0, y0, angle))
    ctx.moveTo(x, y)

    x = x2; y = y1
    if (rotateMode) ({ x, y } = this.rotateCoordinates(x, y, x0, y0, angle))
    ctx.lineTo(x, y)

    x = x2; y = y2
    if (rotateMode) ({ x, y } = this.rotateCoordinates(x, y, x0, y0, angle))
    ctx.lineTo(x, y)

    x = x1; y = y2
    if (rotateMode) ({ x, y } = this.rotateCoordinates(x, y, x0, y0, angle))
    ctx.lineTo(x, y)

    x = x1; y = y1
    if (rotateMode) ({ x, y } = this.rotateCoordinates(x, y, x0, y0, angle))
    ctx.lineTo(x, y)

    ctx.stroke()
  }

  parallelogram(state, x1, y1, x2, y2) {
    const { ctx, rotationDegree: angle } = state

    const degValue = 40

    const x0 = (x2 + x1) / 2
    const y0 = (y2 + y1) / 2

    const rotateMode = Boolean(angle)

    let x, y

    ctx.beginPath()

    x = x1; y = y1
    if (rotateMode) ({ x, y } = this.rotateCoordinates(x, y, x0, y0, angle))
    ctx.moveTo(x, y)

    x = x2 + degValue; y = y1
    if (rotateMode) ({ x, y } = this.rotateCoordinates(x, y, x0, y0, angle))
    ctx.lineTo(x, y)

    x = x2; y = y2
    if (rotateMode) ({ x, y } = this.rotateCoordinates(x, y, x0, y0, angle))
    ctx.lineTo(x, y)

    x = x1 - degValue; y = y2
    if (rotateMode) ({ x, y } = this.rotateCoordinates(x, y, x0, y0, angle))
    ctx.lineTo(x, y)

    x = x1; y = y1
    if (rotateMode) ({ x, y } = this.rotateCoordinates(x, y, x0, y0, angle))
    ctx.lineTo(x, y)

    ctx.stroke()
  }  
  
  circle(state, x1, y1, x2, y2) {
    const { ctx } = state

    const x0 = x2 
    const y0 = y2

    const width = x2 - x1

    ctx.beginPath()
    ctx.arc(x0, y0, width, 0, Math.PI * 2);
    ctx.stroke()
  }

  ellipse(state, instruments, x1, y1, x2, y2) {
    const { ctx, rotationDegree: angle } = state

    const x0 = x2
    const y0 = y2

    ctx.beginPath()

    ctx.ellipse(x0, y0, Math.abs(x2 - x1), Math.abs(y2 - y1), this.rad(angle), 0, 2 * Math.PI)
    ctx.stroke()

    // Draw the ellipse's line of reflection
    // ctx.beginPath()
    // ctx.setLineDash([7, 10])
    
    // ctx.moveTo(x2, y2);
    // ctx.lineTo(Math.abs(x2 - x + x2 + ((Math.abs(x2 - x + x2)) / 10)), y2);
    // ctx.moveTo(x2, y2);
    // ctx.lineTo(Math.abs(x2 + x - x2 - ((Math.abs(x2 - x + x2)) / 10)), y2);
    // ctx.moveTo(x2, y2);
    // ctx.lineTo(x2, Math.abs(y2 + y - y2) - ((Math.abs(y2 + y - y2)) / 3));
    // ctx.moveTo(x2, y2);
    // ctx.lineTo(x2, Math.abs(y2 - y + y2) + ((Math.abs(y2 + y - y2)) / 4));
    
    // Lines insides
    // ctx.moveTo(x2, y2)
    // ctx.lineTo(Math.abs(x2 - x + x2), y2)
    // ctx.moveTo(x2, y2)
    // ctx.lineTo(Math.abs(x2 + x - x2), y2)
    // ctx.moveTo(x2, y2);
    // ctx.lineTo(x2, Math.abs(y2 + y - y2));
    // ctx.moveTo(x2, y2);
    // ctx.lineTo(x2, Math.abs(y2 - y + y2));
    ctx.stroke();
  }

  coordinatesSystem(state, x1, y1, x2, y2) {
    const { ctx, rotationDegree: angle } = state

    const size = 7

    const x0 = x2
    const y0 = y2

    const rotateMode = Boolean(angle)
    let x, y

    ctx.beginPath()

    // Right arrow
    ctx.moveTo(x0, y0)

    x = Math.abs(x0 + x0 - x1); y = y2
    if (rotateMode) ({ x, y } = this.rotateCoordinates(x, y, x0, y0, angle))
    ctx.lineTo(x, y)

    x = Math.abs(x2 - x1 + x2 - size); y = y2 + size
    if (rotateMode) ({ x, y } = this.rotateCoordinates(x, y, x0, y0, angle))
    ctx.lineTo(x, y)

    x = Math.abs(x0 + x0 - x1); y = y2
    if (rotateMode) ({ x, y } = this.rotateCoordinates(x, y, x0, y0, angle))
    ctx.lineTo(x, y)

    x = Math.abs(x2 - x1 + x2 - size); y = y2 - size
    if (rotateMode) ({ x, y } = this.rotateCoordinates(x, y, x0, y0, angle))
    ctx.lineTo(x, y)
    

    // Left line
    ctx.moveTo(x0, y0)

    x = Math.abs(x2 + x1 - x2); y = y2
    if (rotateMode) ({ x, y } = this.rotateCoordinates(x, y, x0, y0, angle))
    ctx.lineTo(x, y)

    // Top arrow
    ctx.moveTo(x0, y0)

    x = x2; y = Math.abs(y2 + y1 - y2)
    if (rotateMode) ({ x, y } = this.rotateCoordinates(x, y, x0, y0, angle))
    ctx.lineTo(x, y)

    x = x2 - size; y = Math.abs(y2 + y1 - y2 + size)
    if (rotateMode) ({ x, y } = this.rotateCoordinates(x, y, x0, y0, angle))
    ctx.lineTo(x, y)

    x = x2; y = Math.abs(y2 + y1 - y2)
    if (rotateMode) ({ x, y } = this.rotateCoordinates(x, y, x0, y0, angle))
    ctx.lineTo(x, y)

    x = x2 + size; y = Math.abs(y2 + y1 - y2 + size)
    if (rotateMode) ({ x, y } = this.rotateCoordinates(x, y, x0, y0, angle))
    ctx.lineTo(x, y)

    // Left bottom
    ctx.moveTo(x0, y0)
    x = x2; y = Math.abs(y2 - y1 + y2)
    if (rotateMode) ({ x, y } = this.rotateCoordinates(x, y, x0, y0, angle))
    ctx.lineTo(x, y)

    ctx.stroke()
  }

  adapQuarter(x, y, x1, y1, width) {
    let w = Math.abs(width)
    
    return {
      numbers:  x1 > x ? ( y1 > y ? [-w, -w, w, w] : [-w, w, w, -w] ) : ( y1 > y ? [w, -w, -w, w] : [w, w, -w, -w]),
      quater:  x1 > x ? ( y1 > y ? 4 : 1) : ( y1 > y ? 3 : 2)
    }

    // if (x < x1 && y > y1) {
    //   return [-w, w, w, -w]
    // }

    // if (x > x1 && y > y1) {
    //   return [w, w, -w, -w]
    // } 
    
    // if (x > x1 && y < y1) {
    //   return [w, -w, -w, w]
    // }

    // if (x < x1 && y < y1) {
    //   return [-w, -w, w, w]
    // }
  }

  rad(angle) { return angle * Math.PI / 180 }

  rotateCoordinates(x, y, x0, y0, angle) {
    angle = this.rad(angle)

    let adaped_x = (x - x0) * Math.cos(angle) - (y - y0) * Math.sin(angle) + x0
    let adaped_y = (x - x0) * Math.sin(angle) + (y - y0) * Math.cos(angle) + y0
  
    return { x: adaped_x, y: adaped_y }
  }

  rotateCoordinatesСouple(couple, angle) {
    let { x1, y1, x2, y2 } = couple

    angle = this.rad(angle);

    // const x0 = (x1 + x2) / 2
    // const y0 = (y1 + y2) / 2

    ({x: x1, y: y1} = this.rotateCoordinates(x1, y1, (x1 + x2) / 2, (y1 + y2) / 2, angle));
    ({x: x2, y: y2} = this.rotateCoordinates(x2, y2, (x1 + x2) / 2, (y1 + y2) / 2, angle));

    return { x1, y1, x2, y2 }
  }

  rotatedSelectedAreaCoordinates(coordinates, angle) {
    const { x1, y1, x2, y2 } = coordinates
    const adapedCoordinates = {}

    let x, y

    const x0 = (x2 + x1) / 2
    const y0 = (y2 + y1) / 2
    
    const rotateMode = Boolean(angle)


    x = x1 
    y = y1
    if (rotateMode) ({ x, y } = this.rotateCoordinates(x1, y1, x0, y0, angle))
    adapedCoordinates.x1 = x
    adapedCoordinates.y1 = y

    x = x2; y = y1
    if (rotateMode) ({ x, y } = this.rotateCoordinates(x2, y1, x0, y0, angle))
    adapedCoordinates.x2 = x
    adapedCoordinates.y2 = y

    x = x2; y = y2
    if (rotateMode) ({ x, y } = this.rotateCoordinates(x2, y2, x0, y0, angle))
    adapedCoordinates.x3 = x
    adapedCoordinates.y3 = y

    x = x1; y = y2
    if (rotateMode) ({ x, y } = this.rotateCoordinates(x1, y2, x0, y0, angle))
    adapedCoordinates.x4 = x
    adapedCoordinates.y4 = y

    return adapedCoordinates
  }

  prepareSelectedAreaCoordinatea(coordinates) {
    // Return max and min values of the rectangle coordinates (4 -> 2)
    const { 
      x1, y1, 
      x2, y2, 
      x3, y3, 
      x4, y4, 
    } =  coordinates
    
    return {
      x1: Math.min(x1, x2, x3, x4),
      y1: Math.min(y1, y2, y3, y4),
      x2: Math.max(x1, x2, x3, x4),
      y2: Math.max(y1, y2, y3, y4),
    }   
  }

  getRotatedCoordinates(selectedAreaCoordinates, deg) {
    // Rotate and transform {x1, y1, x2, y2} to {x1, y1, x2, y2, x3, y3, x4, y4}
    let areaCoorditinates = this.rotatedSelectedAreaCoordinates(selectedAreaCoordinates, deg)
    // Find min and max {x1, y1, x2, y2} in {x1, y1, x2, y2, x3, y3, x4, y4}
    return this.prepareSelectedAreaCoordinatea(areaCoorditinates)
  }
}

