export async function exportRecordings(recordings) {
  const result = await audioToJSON(recordings)
  
  let file = new Blob([JSON.stringify(result)], {type: 'application/json'})

  downloadFile(file, 'audio.json')
}

async function audioToJSON(recordings) {
  const audio = []
  for (const recording of recordings) {
    let base64 = undefined

    if (recording?.base64) {
      // If blob is already preprered
      base64 = recording?.base64
    } else {
      // Convert blob to base64
      base64 = await blobToBase64(recording.blob)
    }
    
    audio.push({ 
      base64, 
      canvasId: recording.canvasId,
      key: recording.key,
      x: recording.x,
      y: recording.y,
      r: recording.r
    })
  }

  return audio
}

async function blobToBase64(blob) {
  return new Promise(resolve => {
    const reader = new window.FileReader()
    
    reader.readAsDataURL(blob)
    
    reader.onloadend = function() {
      let base64 = reader.result
      base64 = base64.split(',')[1]
      resolve(base64)
    }
  })
}