import React from "react";
import { exportPDFfile, exportRecordings } from "../../../utils/export";

/*
  Transform ImageData format to file.json and export new file
*/

export class ExportButton extends React.Component {
  constructor(props) {
    super(props)
    this.methods = this.props.methods
  }

  // fileExport() {
  //   this.props.methods.successfullyExport()
    
  //   let copy = transformPagesToJson(this.props.parentState)
  //   let file = new Blob([copy], {type: 'application/json'})
    
  //   downloadFile(file, 'data.json')
  // }

  exportPDF() {
    if (window.confirm('Do you realy want to export PDF file of the current project?')) {
      exportPDFfile(this.props.parentState)
    }
  }

  exportRecords() {
    if (window.confirm('Do you realy want to export audio records of the current project?')) {
      const { records } = this.props.parentState
      
      if (records.length > 0) {
        exportRecordings(records)
      } else {
        alert('You do not have any records!')
      }
    }
  }

  render() {
    return <div 
      className="possibilitie export" 
      style={{ background: this.props.isActive ? '#ccc' : 'none' }}
      onClick={this.methods.toggleExportMenu}>
      <span className="item">EXPORT</span>
      
      {
        this.props.isActive ? (
          <section className="export-tools">
            <button onClick={() => this.exportPDF()} className="pdf" title="PDF Export">pdf</button>
            <button onClick={() => this.exportRecords()} title="Records Export">records</button>
          </section>
        ) : (
          ''
        )
      }
  </div>
  }
}