import React from "react";
import { setDefaultActive } from "../../../common/functions";
import { defaultCursors } from "../../../common/prototips";

/*
  View and functional of special canvas instrument
*/

export class Instrument extends React.Component {
  constructor(props) {
    super(props)
    this.methods = this.props.methods
    this.instrument = this.props.instrument
  }

  changeInstrument(e, type) {
    let instruments = setDefaultActive(Object.assign(this.props.instruments), type)

    instruments[type].active = !instruments[type].active
    this.methods.updateInstruments(instruments)

    this.copyFunctionalCancel(type, instruments[type].active)
    this.cursorCancel(type, instruments[type].active)
  }

  copyFunctionalCancel(type, flag) {
    if (type !== 'scissors')
      this.methods.copyCancel()
    if (type === 'scissors' && !flag)
      this.methods.copyCancel()
  }

  cursorCancel() {
    let instruments = Object.assign(this.props.instruments)
    instruments.pointer.settings = Object.assign({}, defaultCursors)
    this.methods.updateInstruments(instruments)
  }

  render() {
    // Remove Pointer instrument if window less 500px
    if (window.innerWidth < 500 && this.instrument.name === 'pointer') return <></>
    
    return <div
      title={this.instrument.ru}
      className={`instrument ${this.props.instruments[this.instrument.name].active ? "active" : ''}`}
      onClick={(e) => this.changeInstrument(e, this.instrument.name)}>
      <img src={this.instrument.img} alt={this.instrument.name} />
    </div>
  }
}