import React from "react";

/*
  Functional of pencil instrument
*/

export class _Pencil extends React.Component {
  onMouseDownEvent(state, x, y) {
    let { ctx, instruments } = state

    if (instruments.pencil.active) {
      ctx.beginPath()
      ctx.moveTo (x, y)
    }
  }

  onMouseMoveEvent(state, x, y) {
    let { ctx, instruments, globalColor } = state

    ctx.strokeStyle = globalColor
    ctx.lineJoin = 'round'
    ctx.lineCap = 'round'
    ctx.lineWidth = instruments.pencil.settings.size
    instruments.pencil.settings.dashedMode ? ctx.setLineDash([5, 15]) : ctx.setLineDash([])

    ctx.lineTo (x, y)
    ctx.stroke()
  }

  onMouseUpEvent(state, x, y) {
    let { ctx } = state
    ctx.lineTo (x, y)
    ctx.stroke()
  }
}
