import React from "react";
import { defaultScissorsSettings } from "../common/prototips";
import { imgs } from "../common/imgs";
import { _Pencil } from "./Instruments/compoments/_Pencil";
import { _Figures } from "./Instruments/compoments/_Figures";
import { setDefaultActive } from "../common/functions";

class Canvas extends React.Component {
  constructor(props) {
    super(props)

    this.methods = this.props.methods
    this.state = {
      figureMovingMode: false
    }
  }

  get getX() {
    return this.props.parentState.coordinates.x
  }

  get getY() {
    return this.props.parentState.coordinates.y
  }

  get getPencilY() {
    // 18px is cursor heigth
    return this.props.parentState.coordinates.y + 18
  }

  componentDidMount() {
    this.methods.canvasResave(
      document.getElementById('canvas1'),
      document.getElementById('canvas1')?.getContext('2d'), 1
    )

    window.addEventListener('keypress', e => {
      if (e.key === 'Z' && e.ctrlKey) {
        this.methods.navigationToggle()
      }
    })
  }

  onMouseClickHandler(e) {
    let { instruments, elementsMemory } = this.props.parentState

    if (instruments.glove.active) {
      if (elementsMemory.length) {
        if (this.methods.checkFigures()) {
        }
      }
    }
  }

  onTouchDownHandler(e) {
    this.methods.pressingResave(true)
    this.props.parentState.ctx.save()

    let { ctx, canvas, instruments, elementsMemory } = this.props.parentState
    let { left, top } = canvas.getBoundingClientRect()

    const { clientX, clientY } = e.nativeEvent.touches[0]

    const X = clientX - left
    const Y = clientY - top

    // Pencil
    new _Pencil().onMouseDownEvent(
      this.props.parentState, X, Y
    )

    // Line
    if (instruments.line.active) {
      let { settings } = this.props.parentState.instruments.line

      let lineSettings = Object.assign(settings, {
        beforeDrawing: ctx.getImageData(0, 0, canvas.width, canvas.height),
        x: X,
        y: Y
      })

      this.methods.instrumentSettingsResave('line', lineSettings)
    }

    // Scissors
    if (instruments.scissors.active) {
      let { settings } = this.props.parentState.instruments.scissors

      let scissorsSettings = Object.assign(settings, {
        beforeDrawing: ctx.getImageData(0, 0, canvas.width, canvas.height),
        x: X,
        y: Y
      })

      this.methods.instrumentSettingsResave('scissors', scissorsSettings)
    }

    // Figures
    if (instruments.figure.active) {
      let { settings } = this.props.parentState.instruments.figure

      let figureSettings = Object.assign(settings, {
        beforeDrawing: ctx.getImageData(0, 0, canvas.width, canvas.height),
        x: X,
        y: Y
      })

      this.methods.instrumentSettingsResave('figure', figureSettings)
    }

    // Glove
    if (instruments.glove.active) {
      if (elementsMemory.length) {

        let figure = this.methods.getLastSelectedFifure()

        if (figure.active) {
          // To disactive figure
          if (this.methods.isSelectedElementsExist()) {
            figure.active = false
            this.methods.returnPreviousAction()
          }
        } else {
          // To active figure
          if (this.methods.checkFigures()) {
            new _Figures().drawFigureSelectArea(figure, this.props.parentState, ctx)
            this.pageBackUp()
            figure.active = true
          }
        }

        this.methods.elementsMemoryResave(figure)
        this.methods.instrumentSettingsResave('glove', {x: X, y: Y, resize: this.props.parentState.instruments.glove.settings.resize})
      }
    }

    // Text
    if (instruments.text.active) {
      let { settings } = this.props.parentState.instruments.text

      let textSettings = Object.assign(settings, {
        beforeDrawing: ctx.getImageData(0, 0, canvas.width, canvas.height),
        x: X,
        y: Y
      })

      this.methods.instrumentSettingsResave('text', textSettings)
    }
  }

  onTouchMoveHandler(e) {
    let { instruments, canvas, elementsMemory, ctx, globalColor } = this.props.parentState

    let { left, top } = canvas.getBoundingClientRect()

    const { clientX, clientY } = e.nativeEvent.touches[0]

    const X = clientX - left
    const Y = clientY - top

    this.methods.coordinatesResave({ X, Y })

    // Pencil
    if (instruments.pencil.active) {
      // this.canvasBorderBreak(canvasHover)
      new _Pencil().onMouseMoveEvent(this.props.parentState, X, Y)
    }

    // Line
    if (instruments.line.active) {
      let { x, y } = this.props.parentState.instruments.line.settings
      let lastHistory = this.methods.getLastHistory()

      ctx.putImageData(Object.assign(lastHistory), 0, 0)

      ctx.strokeStyle = globalColor
      ctx.lineJoin = 'round'
      ctx.lineCap = 'round'
      ctx.lineWidth = instruments.line.settings.size
      instruments.line.settings.dashedMode ?  ctx.setLineDash([5, 15]) :  ctx.setLineDash([])

      ctx.beginPath()
      ctx.moveTo(x, y)
      ctx.lineTo(X, Y)
      ctx.stroke()
    }

    // Eraser
    if (instruments.eraser.active) {
      ctx.beginPath()
      let eraserSize = instruments.eraser.settings.size

      ctx.clearRect(
        X - eraserSize, // x
        Y - eraserSize, // y
        eraserSize * 2, // width
        eraserSize * 2, // height
      )
    }

    // Scissors (Select Mode)
    if (instruments.scissors.active) {
      let { x, y } = this.props.parentState.instruments.scissors.settings
      let beforeCopy = this.methods.getLastHistory()

      ctx.putImageData(Object.assign(beforeCopy), 0, 0)

      // Generate dashed preview copy area
      ctx.beginPath()

      ctx.setLineDash([5, 15])
      ctx.strokeStyle = '#000'
      ctx.lineJoin = 'round'
      ctx.lineCap = 'round'
      ctx.lineWidth = 1

      ctx.moveTo(x, y)
      ctx.lineTo(X, y)
      ctx.lineTo(X, Y)
      ctx.lineTo(x, Y)
      ctx.lineTo(x, y)
      ctx.stroke()
    }

    // Scissors (Paste Mode)
    if (instruments.scissors.active && instruments.scissors.settings.pasteMode) {
      let { copy } = this.props.parentState.instruments.scissors.settings
      let beforeCopy = this.methods.getLastHistory()

      ctx.putImageData(Object.assign(beforeCopy), 0, 0)

      const _copy = Object.assign(copy)

      // Generate dashed preview paste area
      ctx.beginPath()
      ctx.putImageData(Object.assign(beforeCopy), 0, 0)
      ctx.putImageData(
        _copy, 
        X - (_copy.width / 2),
        Y - (_copy.height / 2)
      )
    }

    // Figure
    if (instruments.figure.active) {
      let { x, y } = instruments.figure.settings
      let lastHistory = this.methods.getLastHistory()

      ctx.putImageData(Object.assign(lastHistory), 0, 0)

      new _Figures().setStyles(this.props.parentState)
      new _Figures().drawFigure(this.props.parentState, x, y, X, Y)
    }

    // Glove (static)
    if (instruments.glove.active) {
    // if (instruments.glove.active && !pressing) {
      this.methods.checkFiguresHover()
    }

    // Glove (moving and selecting)
    if (instruments.glove.active) {
      if (elementsMemory.length) {
        if (!this.methods.isSelectedElementsExist()) {

          if (!this.state.figureMovingMode) {
            this.methods.returnPreviousAction()
          }

          this.setState({figureMovingMode: true})

          let { x, y } = instruments.glove.settings
          let _X =  X - x
          let _Y = Y - y

          // Delete previus view of figure (in moving mode)
          let beforeCopy = this.methods.getLastHistory()
          ctx.putImageData(Object.assign(beforeCopy), 0, 0)

          let { coordinates: { figure: { x1, y1, x2, y2 } }, color, fillColor, dashedMode, types } = this.methods.getLastSelectedFifure()

          // Set styles of current figure
          ctx.strokeStyle = color
          dashedMode ?  ctx.setLineDash([5, 15]) :  ctx.setLineDash([])

          if (fillColor) {
            if (!types.coordinatesSystem) {
              ctx.fillStyle = instruments.figure.settings.fillColor
              ctx.fill()
            }
          }

          // Redraw figure
          if (instruments.glove.settings.resize) {
            new _Figures().drawFigure(this.props.parentState, x1, y1, x2 + _X, y2 + _Y)
          } else {
            new _Figures().drawFigure(this.props.parentState, x1 + _X, y1 + _Y, x2 + _X, y2 + _Y)
          }
        }
      }
    }

    // Text
    if (instruments.text.active) {
      let { content, boldMode, italicMode, size } = this.props.parentState.instruments.text.settings
      let lastHistory = this.methods.getLastHistory()

      ctx.putImageData(Object.assign(lastHistory), 0, 0)

      ctx.beginPath()

      ctx.font = `${boldMode ? 'bold': ''} ${italicMode ? 'italic': ''} ${size}px Arial`
      ctx.fillText(content || 'Text', X, Y)
    }
  }

  onTouchUpHandler(e) {
    this.methods.pressingResave(false)
    let { ctx, instruments, canvas, elementsMemory } = this.props.parentState

    let { left, top } = canvas.getBoundingClientRect()

    const { clientX, clientY } = e.nativeEvent.changedTouches[0]

    const X = clientX - left
    const Y = clientY - top

    // Pencil
    if (instruments.pencil.active) {
      new _Pencil().onMouseUpEvent(this.props.parentState, X, Y)
    }

    // Line
    if (instruments.line.active) {
      ctx.lineTo (X, Y)
      ctx.stroke()
    }

    // Eraser
    if (instruments.eraser.active) {
      ctx.beginPath()
      let eraserSize = instruments.eraser.settings.size

      ctx.clearRect(
        X - eraserSize, // x
        Y - eraserSize, // y
        eraserSize * 2, // width
        eraserSize * 2, // height
      )
    }

    // Copy data by scissors
    if(instruments.scissors.active && !instruments.scissors.settings.pasteMode) {
      let { x, y } = this.props.parentState.instruments.scissors.settings

      if (X - x && Y - y) {
        let beforeCopy = this.methods.getLastHistory()

        // Remove dashed preview copy area
        ctx.beginPath()
        ctx.putImageData(Object.assign(beforeCopy), 0, 0)

        // Remember last coordinates and copy area
        let copy = ctx.getImageData(x, y, X - x, Y - y)
        let settings = Object.assign(
          this.props.parentState.instruments.scissors.settings,
          {pasteMode: true, copy}
        )

        this.methods.instrumentSettingsResave('scissors', settings)

        ctx.putImageData(
          copy, 
          X - copy.width / 2, 
          Y - copy.height / 2
        )

        // Notifications
        this.methods.showCopySuccessMessage()
        this.methods.pasteSuccessMessage(true)
        this.methods.copyCancelButtonState(true)
      }

      return 0
    }

    // Paste data by scissors
    if (instruments.scissors.active && instruments.scissors.settings.pasteMode) {
      // Notifications
      this.methods.pasteSuccessMessage(false)
      this.methods.copyCancelButtonState(false)

      this.methods.instrumentSettingsResave('scissors', Object.assign({}, defaultScissorsSettings))
      this.pageBackUp()
      return 0
    }

    // Figure
    if (instruments.figure.active) {
      this.pageBackUp()

      // Draw and save figure border
      let { x: x1, y: y1 } = instruments.figure.settings

      new Promise((resolve, reject) => {
        new _Figures().onMouseUpEvent(this.props.parentState, x1, y1, X, Y, this.methods.elementsMemoryResave)
        this.pageBackUp()

        resolve(true)
      })
      .then(flag => {
         // Resave instrument
        let _instruments = setDefaultActive(Object.assign(instruments), 'glove')
        _instruments.glove.active = flag
        this.methods.updateInstruments(_instruments)
      })

      return 0
    }

    // Glove
    if (instruments.glove.active) {
      if (elementsMemory.length) {
        if (this.state.figureMovingMode) {
          this.coordinatesByGloveResave(X, Y)
          this.methods.checkFiguresHover()

          this.pageBackUp()
        }

        this.setState({ figureMovingMode: false })
      }

      return 0
    }

    this.pageBackUp()
    ctx.restore()
  }

  onMouseDownHandler(e) {
    this.methods.pressingResave(true)
    this.props.parentState.ctx.save()

    let { ctx, canvas, instruments, elementsMemory } = this.props.parentState

    // Glove
    if (instruments.glove.active) {
      if (elementsMemory.length) {

        let figure = this.methods.getLastSelectedFifure()

        if (figure.active) {
          // To disactive figure
          if (this.methods.isSelectedElementsExist()) {
            figure.active = false
            this.methods.returnPreviousAction()
          }
        } else {
          // To active figure
          if (this.methods.checkFigures()) {
            new _Figures().drawFigureSelectArea(figure, this.props.parentState, ctx)
            this.pageBackUp()
            figure.active = true
          }
        }

        this.methods.elementsMemoryResave(figure)
        this.methods.instrumentSettingsResave('glove', {x: this.getX, y: this.getY, resize: this.props.parentState.instruments.glove.settings.resize})
      }
    }

    // Pencil
    new _Pencil().onMouseDownEvent(this.props.parentState, this.getX, this.getPencilY)

    // Line
    if (instruments.line.active) {
      let { settings } = this.props.parentState.instruments.line

      let lineSettings = Object.assign(settings, {
        beforeDrawing: ctx.getImageData(0, 0, canvas.width, canvas.height),
        x: this.getX,
        y: this.getY
      })

      this.methods.instrumentSettingsResave('line', lineSettings)
    }

    // Scissors
    if (instruments.scissors.active) {
      let { settings } = this.props.parentState.instruments.scissors

      let scissorsSettings = Object.assign(settings, {
        beforeDrawing: ctx.getImageData(0, 0, canvas.width, canvas.height),
        x: this.getX,
        y: this.getY
      })

      this.methods.instrumentSettingsResave('scissors', scissorsSettings)
    }

    // Figures
    if (instruments.figure.active) {
      let { settings } = this.props.parentState.instruments.figure

      let figureSettings = Object.assign(settings, {
        beforeDrawing: ctx.getImageData(0, 0, canvas.width, canvas.height),
        x: this.getX,
        y: this.getY
      })

      this.methods.instrumentSettingsResave('figure', figureSettings)
    }

    // Text
    if (instruments.text.active) {
      let { settings } = this.props.parentState.instruments.text

      let textSettings = Object.assign(settings, {
        beforeDrawing: ctx.getImageData(0, 0, canvas.width, canvas.height),
        x: this.getX,
        y: this.getY
      })

      this.methods.instrumentSettingsResave('text', textSettings)
    }
  }

  onMouseMoveHandler(e) {
    let { instruments, ctx, canvas, canvasHover, globalColor, pressing, elementsMemory } = this.props.parentState

    let { left, top } = canvas.getBoundingClientRect()

    this.methods.coordinatesResave({
      x: e.nativeEvent.clientX - left,
      y: e.nativeEvent.clientY - top
    })

    // Glove (static)
    if (instruments.glove.active && !pressing) {
      this.methods.checkFiguresHover()
    }

    // Glove (moving and selecting)
    if (instruments.glove.active && pressing) {
      if (elementsMemory.length) {
        if (!this.methods.isSelectedElementsExist()) {

          if (!this.state.figureMovingMode) {
            this.methods.returnPreviousAction()
          }

          this.setState({figureMovingMode: true})

          let { x, y } = instruments.glove.settings
          let X =  this.getX - x
          let Y = this.getY - y

          // Delete previus view of figure (in moving mode)
          let beforeCopy = this.methods.getLastHistory()
          ctx.putImageData(Object.assign(beforeCopy), 0, 0)

          let { coordinates: { figure: { x1, y1, x2, y2 } }, color, fillColor, dashedMode, types } = this.methods.getLastSelectedFifure()
          // Set styles of current figure
          ctx.strokeStyle = color
          dashedMode ?  ctx.setLineDash([5, 15]) :  ctx.setLineDash([])

          // Redraw figure
          if (instruments.glove.settings.resize) {
            new _Figures().drawFigure(this.props.parentState, x1, y1, x2 + X, y2 + Y)
          } else {
            new _Figures().drawFigure(this.props.parentState, x1 + X, y1 + Y, x2 + X, y2 + Y)
          }

          // Fill figure
          if (fillColor) {
            if (!types.coordinatesSystem) {
              ctx.fillStyle = instruments.figure.settings.fillColor
              ctx.fill()
            }
          }
        }
      }
    }

    // Pencil
    if (instruments.pencil.active && pressing) {
      this.canvasBorderBreak(canvasHover)
      new _Pencil().onMouseMoveEvent(this.props.parentState, this.getX, this.getPencilY)
    }

    // Line
    if (instruments.line.active && pressing) {
      let { x, y } = this.props.parentState.instruments.line.settings
      let lastHistory = this.methods.getLastHistory()

      this.canvasBorderBreak(canvasHover)

      ctx.putImageData(Object.assign(lastHistory), 0, 0)

      ctx.strokeStyle = globalColor
      ctx.lineJoin = 'round'
      ctx.lineCap = 'round'
      ctx.lineWidth = instruments.line.settings.size
      instruments.line.settings.dashedMode ?  ctx.setLineDash([5, 15]) :  ctx.setLineDash([])

      ctx.beginPath()
      ctx.moveTo(x, y)
      ctx.lineTo(this.getX, this.getY)
      ctx.stroke()
    }

    // Eraser
    if (instruments.eraser.active && pressing) {
      ctx.beginPath()
      let eraserSize = instruments.eraser.settings.size

      ctx.clearRect(this.getX, this.getY, eraserSize, eraserSize)
    }

    // Scissors (Select Mode)
    if (instruments.scissors.active && pressing) {
      let { x, y } = this.props.parentState.instruments.scissors.settings
      let beforeCopy = this.methods.getLastHistory()

      ctx.putImageData(Object.assign(beforeCopy), 0, 0)

      // Generate dashed preview copy area
      ctx.beginPath()

      ctx.setLineDash([5, 15])
      ctx.strokeStyle = '#000'
      ctx.lineJoin = 'round'
      ctx.lineCap = 'round'
      ctx.lineWidth = 1

      ctx.moveTo(x, y)
      ctx.lineTo(this.getX, y)
      ctx.lineTo(this.getX, this.getY)
      ctx.lineTo(x, this.getY)
      ctx.lineTo(x, y)
      ctx.stroke()
    }

    // Scissors (Paste Mode)
    if (instruments.scissors.active && instruments.scissors.settings.pasteMode) {
      let { copy } = this.props.parentState.instruments.scissors.settings
      let beforeCopy = this.methods.getLastHistory()

      ctx.putImageData(Object.assign(beforeCopy), 0, 0)

      // Generate dashed preview paste area
      ctx.beginPath()
      ctx.putImageData(Object.assign(beforeCopy), 0, 0)
      ctx.putImageData(Object.assign(copy), this.getX, this.getY)
    }

    // Figure
    if (instruments.figure.active && pressing) {
      let { x, y } = instruments.figure.settings

      this.canvasBorderBreak(canvasHover)

      let lastHistory = this.methods.getLastHistory()
      ctx.putImageData(Object.assign(lastHistory), 0, 0)

      new _Figures().setStyles(this.props.parentState)
      new _Figures().drawFigure(this.props.parentState, x, y, this.getX, this.getY)
    }

    // Text
    if (instruments.text.active && pressing) {
      let { content, boldMode, italicMode, size } = this.props.parentState.instruments.text.settings
      let lastHistory = this.methods.getLastHistory()

      this.canvasBorderBreak(canvasHover)

      ctx.putImageData(Object.assign(lastHistory), 0, 0)

      // ctx.strokeStyle = globalColor
      // ctx.lineJoin = 'round'
      // ctx.lineCap = 'round'
      // ctx.lineWidth = instruments.text.settings.size
      // instruments.text.settings.dashedMode ?  ctx.setLineDash([5, 15]) :  ctx.setLineDash([])

      ctx.beginPath()

      ctx.font = `${boldMode ? 'bold': ''} ${italicMode ? 'italic': ''} ${size}px Arial`
      ctx.fillText(content || 'Тестовая строчка', this.getX, this.getY)
    }

    ctx.restore()
  }

  onMouseUpHandler(e) {
    this.methods.pressingResave(false)
    let { ctx, elementsMemory } = this.props.parentState
    let instr = this.props.parentState.instruments

    // Pencil
    if (instr.pencil.active) {
      new _Pencil().onMouseUpEvent(this.props.parentState, this.getX, this.getPencilY)
    }

    // Eraser
    if (instr.eraser.active && this.props.parentState.pressing) {
      ctx.beginPath()
      let eraserSize = instr.eraser.settings.size

      ctx.clearRect(this.getX, this.getY, eraserSize, eraserSize)
    }

    // Glove
    if (instr.glove.active) {
      if (elementsMemory.length) {
        if (this.state.figureMovingMode) {
          this.coordinatesByGloveResave(this.getX, this.getY)
          this.methods.checkFiguresHover()

          this.pageBackUp()
        }

        this.setState({figureMovingMode: false})
      }

      return 0
    }

    // Line
    if (instr.line.active) {
      ctx.lineTo (this.getX, this.getY)
      ctx.stroke()
    }

    // Figure
    if (instr.figure.active) {
      this.pageBackUp()

      // Draw and save figure border
      let { x: x1, y: y1 } = instr.figure.settings

      new Promise((resolve, reject) => {
        new _Figures().onMouseUpEvent(this.props.parentState, x1, y1, this.getX, this.getY, this.methods.elementsMemoryResave)
        this.pageBackUp()

        resolve(true)
      })
      .then(flag => {
         // Resave instrument
        let instruments = setDefaultActive(Object.assign(instr), 'glove')
        instruments.glove.active = flag
        this.methods.updateInstruments(instruments)
      })

      return 0
    }

    // Copy data by scissors
    if(instr.scissors.active && !instr.scissors.settings.pasteMode) {
      let { x, y } = this.props.parentState.instruments.scissors.settings

      if (this.getX - x && this.getY - y) {
        let beforeCopy = this.methods.getLastHistory()

        // Remove dashed preview copy area
        ctx.beginPath()
        ctx.putImageData(Object.assign(beforeCopy), 0, 0)

        // Remember last coordinates and copy area
        let copy = ctx.getImageData(x, y, this.getX - x, this.getY - y)
        let settings = Object.assign(
          this.props.parentState.instruments.scissors.settings,
          {pasteMode: true, copy}
        )

        this.methods.instrumentSettingsResave('scissors', settings)

        ctx.putImageData(copy, this.getX, this.getY)

        // Notifications
        this.methods.showCopySuccessMessage()
        this.methods.pasteSuccessMessage(true)
        this.methods.copyCancelButtonState(true)
      }

      return 0
    }

    // Paste data by scissors
    if (instr.scissors.active && instr.scissors.settings.pasteMode ) {
      // Notifications
      this.methods.pasteSuccessMessage(false)
      this.methods.copyCancelButtonState(false)

      this.methods.instrumentSettingsResave('scissors', Object.assign({}, defaultScissorsSettings))
      this.pageBackUp()
      return 0
    }

    // Poiner
    if (instr.pointer.active || instr.paper.active) return 0

    this.pageBackUp()
    ctx.restore()
  }

  coordinatesByGloveResave(x_coordinate, y_coordinate) {
    // Resave new coordinates of figure (after moving or resize)
    let { elementsMemory, instruments, rotationDegree } = this.props.parentState

    if (elementsMemory.length) {
      let { x, y } = instruments.glove.settings
      let figure = this.methods.getLastSelectedFifure()

      let {
        coordinates: {
          figure: { x1, y1, x2, y2 },
          selectedArea: { x1: _x1, y1: _y1, x2: _x2, y2: _y2 }
        }
      } = figure

      let X =  x_coordinate - x
      let Y = y_coordinate - y

      // Resave coodinates by mode
      if (instruments.glove.settings.resize) {
        // Resize mode
        let _selectedAreaCoordinates = new _Figures().adapSelectingArea(figure.type, x1, y1, x2 + X, y2 + Y)
        let rotatedCoordinates = new _Figures().getRotatedCoordinates(_selectedAreaCoordinates, rotationDegree)

        figure.coordinates = {
          figure: {x1, y1, x2: x2 + X, y2: y2 + Y},
          selectedArea: _selectedAreaCoordinates,
          rotatedSelectedAreaCoordinates: rotatedCoordinates
        }
      } else {
        let coordinates = {x1: _x1 + X, y1: _y1 + Y, x2: _x2 + X, y2: _y2 + Y}
        let rotatedCoordinates = new _Figures().getRotatedCoordinates(coordinates, rotationDegree)

        // Moving mode
        figure.coordinates = {
          figure: {x1: x1 + X, y1: y1 + Y, x2: x2 + X, y2: y2 + Y},
          selectedArea: {x1: _x1 + X, y1: _y1 + Y, x2: _x2 + X, y2: _y2 + Y},
          rotatedSelectedAreaCoordinates: rotatedCoordinates
        }
      }

      this.methods.elementsMemoryResave(figure)
    }
  }

  finishUp(ctx) {
    this.pageBackUp()
    ctx.restore()
  }

  canvasBorderBreak(hover) {
    // User leaves canvas area while using an instrument
    if (!hover) {
      this.methods.pressingResave(false)
      this.pageBackUp()
    }
  }

  pageBackUp() {
    this.methods.pageBackUp(this.props.parentState.activeCanvasId)
  }

  render() {
    let intr = this.props.parentState.instruments
    let pencilClassCursor = intr.pencil.active ? 'pencil-active-cursor' : ''
    let pencilClass = intr.line.active || (intr.scissors.active && !intr.scissors.settings.pasteMode)? 'pencil-active' : ''
    let eraserClass = intr.eraser.active ? `eraser-active-${intr.eraser.settings.size }` : ''
    let cursorPointer = this.props.parentState.cursorPointer ? `canvas-cursor-pointer` : ''
    let movingPointer = this.props.parentState.movingPointer ? `moving-cursor-pointer` : ''
    let resizePointer = this.props.parentState.resizePointer ? `resize-cursor-pointer` : ''
    let cursorsClass = intr.pointer.settings.redPoint ? `redPoint-active` : '' || intr.pointer.settings.arrowRight ? `arrowRight-active` : ''
    let canvasClass = `canvas-global ${eraserClass} ${pencilClass} ${cursorsClass} ${pencilClassCursor} ${cursorPointer} ${movingPointer} ${resizePointer}`
    const canvasWorkArea = window.innerWidth < 500 ? (42 + 42 + 2) : (69 + 69 + 2)
    let canvasWidth = this.props.parentState.navigation ? window.innerWidth - canvasWorkArea : window.innerWidth
    return <div className="canvas-list">
      {this.props.parentState.pages.map((canvasObj, idx) => {
        if (canvasObj.id === this.props.parentState.activeCanvasId)
          return <canvas
            key={idx}
            className={canvasClass}
            style={{backgroundSize: intr.paper.settings.size}}
            height="600"
            width={canvasWidth}
            onClick={e => this.onMouseClickHandler(e)}
            onMouseDown={e => this.onMouseDownHandler(e)}
            onMouseMove={e => this.onMouseMoveHandler(e)}
            onMouseUp={e => this.onMouseUpHandler(e)}
            onKeyPress={e => this.onKeyPressHandler(e)}
            onTouchStart={e => this.onTouchDownHandler(e)}
            onTouchMove={e => this.onTouchMoveHandler(e)}
            onTouchEnd={e => this.onTouchUpHandler(e)}
            id={`canvas${canvasObj.id}`}>
            </canvas>
        else return ''
      })}
      {!this.props.parentState.navigation ? <button className="leave-full-page-mode" onClick={this.methods.navigationToggle}>
        <img src={imgs.leaveFullPage} alt="leave" />
      </button> : ''}
      
      <img src="" className="image-for-import" style={{opacity: 0}} height="200" alt="preview..." />
    </div>
  }
}

export default Canvas;
