import React from "react";
import { imgs } from "../../common/imgs";
import { Instrument } from "./compoments/Instrument";

class Instruments extends React.Component {
  constructor(props) {
    super(props)
    this.state = props.state
  }

  get instrumentsList() {
    return [
      {name: 'pencil', img: imgs.pencil, ru: 'Карандаш'},
      {name: 'eraser', img: imgs.eraser, ru: 'Стерка'},
      {name: 'line', img: imgs.lineIcon, ru: 'Линия'},
      {name: 'scissors', img: imgs.copy, ru: 'Ножницы'},
      {name: 'figure', img: imgs.cube, ru: 'Фигуры'},
      // {name: 'glove', img: imgs.glove, ru: 'Перчатка'},
      {name: 'text', img: imgs.text, ru: 'Текст'},
      {name: 'pointer', img: imgs.pointer, ru: 'Указка'},
      {name: 'paper', img: imgs.paper, ru: 'Выбор размера клетки'},
    ]
  }

  render() {
    return (
      <nav className="coloring-instruments">
        {this.instrumentsList.map((item, idx) => {
            return <Instrument 
              key={idx}
              methods={{
                updateInstruments: this.props.updateInstruments,
                copyCancel: this.props.copyCancel
              }} 
              instruments={this.state.instruments} 
              instrument={item}
              idx={idx}
          />
        })}
      </nav>
    );
  }
}

export default Instruments;