import React from "react";
import { imgs } from "../../../common/imgs";

/*
  List of canvas pages & create and toggle functional
*/

export class PagesList extends React.Component {
  constructor(props) {
    super(props)
    this.methods = this.props.methods
  }

  addPage() {
    new Promise((resolve, reject) => {
      this.methods.createNewPage()
      resolve('1')
    })
    .then(() => {
      return this.toogleToEnd()
    })
    .then(displayedPages => {
      this.methods.displayedPagesResave(displayedPages)
    })
  }

  toogleToEnd() {
    let pagesNums = this.props.parentState.pagesNums()
    let lastPagesNums = pagesNums[pagesNums.length - 1]
    let displayedPages = Object.assign(this.props.parentState.displayedPages)

    displayedPages = [(lastPagesNums - 2), (lastPagesNums - 1), lastPagesNums]

    return displayedPages
  }

  scrollPages(type) {
    let { displayedPages, activeCanvasId } = this.props.parentState
    let pagesNums = this.props.parentState.pagesNums()
    let lastDisplayedPages = displayedPages[displayedPages.length - 1]
    let lastPagesNums = pagesNums[pagesNums.length - 1]

    if (type === 'up') {
      if (displayedPages[0] > 1) {
        displayedPages = Object.assign(displayedPages).map(n => --n)
        this.methods.changePage(activeCanvasId - 1)
      }
    }

    if (type === 'down') {
      if (lastDisplayedPages < lastPagesNums) {
        displayedPages = Object.assign(displayedPages).map(n => ++n)
        this.methods.changePage(activeCanvasId + 1)
      }
    }

    this.methods.displayedPagesResave(displayedPages)
  }

  render() {
    return <div className="pages">
    {this.props.parentState.pages.length > 3  ? <div 
      className={`possibilitie page`} 
      title={`Move top`} 
      onClick={() => this.scrollPages('up')}>
      <img src={imgs.arrowTop} alt="scroll"/>
    </div> : ''}
    {this.props.parentState.pages.map((canvasObj, idx) => {
      if (this.props.parentState.displayedPages.includes(++idx))
        return <div
          key={idx} 
          className={`possibilitie page ${this.props.parentState.activeCanvasId === canvasObj.id ? 'active' : ''}`} 
          title={`Open page #${canvasObj.id}`} 
          onClick={() => this.methods.changePage(canvasObj.id)}>
          {
            this.props.parentState.records
              .some(rec => rec.canvasId === canvasObj.id) ? (
                <div className="audio">
                  <img src={imgs.voice} alt="voice" />
                </div>
              ) : (
                ''
              )
          }

          <span>{canvasObj.id}</span>
        </div>
      else return ''
    })}
    {this.props.parentState.pages.length > 3 ? <div 
      className={`possibilitie page`} 
      title={`Move down`} 
      onClick={() => this.scrollPages('down')}>
      <img src={imgs.arrowBottom} alt="scroll"/>
    </div> : ''}
    <div 
      className={`possibilitie page create-new-page`} 
      title={`Добавить страницу`} 
      onClick={() => this.addPage()}>
      <img src={imgs.add} alt="addPage"/>
    </div>
  </div>
  }
}