import React from "react"
import { imgs } from "../../../common/imgs"

export class SvgAudio extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      pressed: false,
      moving: false
    }
    this.methods = props.methods
  }

  onMouseDownHandler(event) {
    if (this.props.state.activeRecord) {
      this.setState({ pressed: true })
    }
  }

  onTouchDownHandler(event) {
    if (this.props.state.activeRecord) {
      this.setState({ pressed: true })
    }
  }

  onMouseMoveHandler(event) {
    const { activeRecord } = this.props.state
    const { pressed } = this.state

    if (activeRecord && pressed) {
      let div = document.querySelector('.moving-audio')
      
      if (div) {
        this.setState({ moving: true })
        let rect = div.getBoundingClientRect()
        let x = event.clientX - rect.left
        let y = event.clientY - rect.top 
  
        let records = Object.assign(this.props.state.records)
        records = records.map(record => {
          if (record.key === activeRecord) {
            record.x = x
            record.y = y
          } 
  
          return record
        })
  
        this.methods.recordsResave(records)
      }
    }
  }

  onTouchMoveHandler(event) {
    const { activeRecord } = this.props.state
    const { pressed } = this.state

    if (activeRecord && pressed) {
      let div = document.querySelector('.moving-audio')
      
      if (div) {
        this.setState({ moving: true })
        
          let rect = div.getBoundingClientRect()

        const { clientX, clientY } = event.nativeEvent.touches[0]

        let x = clientX - rect.left
        let y = clientY - rect.top 
  
        let records = Object.assign(this.props.state.records)
        records = records.map(record => {
          if (record.key === activeRecord) {
            record.x = x
            record.y = y
          } 
  
          return record
        })
  
        this.methods.recordsResave(records)
      }
    }
  }

  onMouseUpHandler(event) {
    if (this.props.state.activeRecord) {
      this.setState({ 
        pressed: false, 
        moving: false
      })
    }

    this.methods.resaveActiveRecord(null)
  }

  onTouchUpHandler(event) {
    if (this.props.state.activeRecord) {
      this.setState({ 
        pressed: false, 
        moving: false
      })
    }

    this.methods.resaveActiveRecord(null)
  }
  
  onMouseToggle(event, key) { 
    this.methods.resaveActiveRecord(key === this.props.state.activeRecord ? null : key)
  }

  render() { 
    return <div 
      className={`moving-audio ${this.state.moving ? 'moving' : ''}`} 
      onMouseDown={e => this.onMouseDownHandler(e)}
      onMouseMove={e => this.onMouseMoveHandler(e)}
      onMouseUp={e => this.onMouseUpHandler(e)}
      onTouchStart={e => this.onTouchDownHandler(e)}
      onTouchMove={e => this.onTouchMoveHandler(e)}
      onTouchEnd={e => this.onTouchUpHandler(e)}
      style={{
        width: this.methods.areaWidth,
        height: this.methods.areaHeigth
      }}>
        {
          this.props.state.records.map(record => {
            let className = 'audio-circle'

            if (this.props.state.activeRecord === record.key)
              className += ' active'
            
            return this.props.state.activeCanvasId === record.canvasId ? <div          
              className={className}
              onClick={e => this.onMouseToggle(e, record.key)}
              style={{
                top: record.y, 
                left: record.x
              }}
              key={record.key}>
                <img src={imgs.voice} alt="audio" />
              </div> : ''
          })
        }
    </div>  
  }
}