import React from "react";
import { colors, defaultCursors, emptyFiguresStates, fillColors, fontSizeNumbers } from "../common/prototips";
import { imgs } from "../common/imgs";
import { _Figures } from "./Instruments/compoments/_Figures";

class Header extends React.Component {
  constructor(props) {
    super(props)
    this.methods = this.props.methods
    this.state = Object.assign(props.state, {toggleFillingPanel: false})
  }

  gloveResizMode(flag) {
    let instruments = Object.assign(this.state.instruments)
    instruments.glove.settings.resize = flag
    this.props.updateInstruments(instruments)
  }

  instrumentSize(size, type) {
    this.props.state.ctx.beginPath();
    let instruments = Object.assign(this.state.instruments)
    instruments[type].settings.size = size
    this.props.updateInstruments(instruments)
  }

  figureFillingColor(color) {
    let instruments = Object.assign(this.state.instruments)
    instruments.figure.settings.fillColor = color
    this.props.updateInstruments(instruments)

    // Hide panel
    this.setState({toggleFillingPanel: false})
  }
  
  instrumentColor(color, type) {
    this.props.state.ctx.beginPath()
    this.props.globalColorResave(color)
    
    // Local color resave
    // let instruments = Object.assign(this.state.instruments)
    // instruments[type].settings.color = color
    // this.props.updateInstruments(instruments)
  }

  toggleFillingPanel() {
    this.setState({toggleFillingPanel: !this.state.toggleFillingPanel})
  }
  
  setDashedMode(type) {
    this.props.state.ctx.beginPath();
    let instruments = Object.assign(this.state.instruments)
    instruments[type].settings.dashedMode = !instruments[type].settings.dashedMode
    this.props.updateInstruments(instruments)
  }

  setFigure(figure) {
    let instruments = Object.assign(this.state.instruments)
    let { types } = instruments.figure

    instruments.figure.selected = figure

    types = Object.assign(types, emptyFiguresStates)
    types[figure] = !types[figure]
    this.props.updateInstruments(instruments)
  }

  setTextSettings(type, value) {
    let instruments = Object.assign(this.state.instruments)
    switch(type) {
      case 'boldMode':
        instruments.text.settings[type] = !instruments.text.settings[type]
        break
      case 'italicMode':
        instruments.text.settings[type] = !instruments.text.settings[type]
        break
      case 'content':
        instruments.text.settings[type] = value
        break
      case 'size':
        instruments.text.settings[type] = value
        break
      default: break
    }

    this.props.updateInstruments(instruments)
  }

  setPointerSettings(type) {
    let instruments = Object.assign(this.state.instruments)
    switch(type) {
      case 'arrowRight':
        instruments.pointer.settings = Object.assign({}, defaultCursors, {[type]: true})
        break
      case 'redPoint':
        instruments.pointer.settings = Object.assign({}, defaultCursors, {[type]: true})
        break
      default: break
    }

    this.props.updateInstruments(instruments)
  }

  increaseRotationDegree(deg) {
    if (this.methods.isSelectedElementsExist()) {
      let { rotationDegree: degree } = this.props.state

      degree += deg
      if (degree >= 360 || degree < 0) degree = 0

      new Promise((resolve) => {
        this.methods.resaveRotationDegree(degree)
        resolve(true)
      })
      .then(() => this.rotateFigure())
    }
  }

  getLastFigure() {
    return new Promise((resolve) => {
      const figure = this.methods.getLastSelectedFifure()
      resolve(figure)
    })
  }

  rotateFigure() {
    const { ctx } = this.props.state

    this.getLastFigure()
      .then(figure => {
        // Cancel previous figure's border
        this.methods.returnPreviousAction()
        // Cancel previous figure
        this.methods.returnPreviousAction()

        return figure
      })
      .then(figure => {
        let { x1, y1, x2, y2 } = figure.coordinates.figure
        let { fillColor, types } = figure

        new _Figures().setStyles(this.props.state)

        new _Figures().drawFigure(this.props.state, x1, y1, x2, y2)

        // Fill after rotate
        if (fillColor) {
          if (!types.coordinatesSystem) {
            ctx.fillStyle = fillColor
            ctx.fill()
          }
        }

        this.pageBackUp()

        new _Figures().onMouseUpEvent(this.props.state, x1, y1, x2, y2, this.methods.elementsMemoryResave)
        this.pageBackUp()

        ctx.restore()
      })
  }

  pageBackUp() {
    this.methods.pageBackUp(this.props.state.activeCanvasId)
  }

  render() {
    const instrumentStatement = (n, type) => (n === this.state.instruments[type].settings.size) ? "active" : ''
    const figureStatement = type => this.state.instruments.figure.types[type] ? "active" : ''
    let fillColor = fillColors.filter(color => color === this.state.instruments.figure.settings.fillColor)
    return (
      <header className="coloring-menu">
        <div className="functional">
          {this.state.instruments.pencil.active ? <div className="instrument-preview">
            <h4>{this.state.instruments.pencil.en}</h4>
            <div className="block desktop-view">
              <div className="addtional-blocks">
                <div className={`addtional-block ${instrumentStatement(2, 'pencil')}`} onClick={() => this.instrumentSize(2, 'pencil')}>
                  <img src={imgs.smallCurve} alt="curve" />
                </div>
                <div className={`addtional-block ${instrumentStatement(4, 'pencil')}`} onClick={() => this.instrumentSize(4, 'pencil')}>
                  <img src={imgs.mediumCurve} alt="curve"/>
                </div>
                <div className={`addtional-block ${instrumentStatement(6, 'pencil')}`} onClick={() => this.instrumentSize(6, 'pencil')}>
                  <img src={imgs.hugeCurve} alt="curve"/>
                </div>
                <div className={`addtional-block ${this.state.instruments.pencil.settings.dashedMode ? 'active' : ''}`} onClick={() => this.setDashedMode('pencil')}>
                  <img src={imgs.dashedCurve} alt="curve"/>
                </div>
              </div>
                <div className="colors">
                  {colors.map((color, idx) => <div
                    key={idx}
                    onClick={() => this.instrumentColor(color, 'pencil')} 
                    style={{backgroundColor: color}}
                    className="color">
                      {this.props.state.globalColor === color ? <div></div> : ''}
                    </div>)}
                </div>
            </div>
            <div className="block phone-view">
              <div className="filling">
                <div className={`filling-instrumet ${this.state.toggleFillingPanel ? 'active' : ''}`} onClick={() => this.toggleFillingPanel()}>
                  {/* {fillColor.length ? <div
                    className={`color ${fillColor[0].includes('fff') ? 'add-border' : ''}`}
                    style={{backgroundColor: fillColor[0]}}>
                  </div> : <img src={imgs.filling} alt="filling"/>} */}
                  <img src={imgs.settings} alt="filling"/>
                </div>
                {this.state.toggleFillingPanel && <div className="filling-panel">
                <div className="filling-panel-block">
                    <p>Pencil size</p>
                    <div className="addtional-blocks">
                      <div className={`addtional-block ${instrumentStatement(2, 'pencil')}`} onClick={() => this.instrumentSize(2, 'pencil')}>
                        <img src={imgs.smallCurve} alt="curve" />
                      </div>
                      <div className={`addtional-block ${instrumentStatement(4, 'pencil')}`} onClick={() => this.instrumentSize(4, 'pencil')}>
                        <img src={imgs.mediumCurve} alt="curve"/>
                      </div>
                      <div className={`addtional-block ${instrumentStatement(6, 'pencil')}`} onClick={() => this.instrumentSize(6, 'pencil')}>
                        <img src={imgs.hugeCurve} alt="curve"/>
                      </div>
                      <div className={`addtional-block ${this.state.instruments.pencil.settings.dashedMode ? 'active' : ''}`} onClick={() => this.setDashedMode('pencil')}>
                        <img src={imgs.dashedCurve} alt="curve"/>
                      </div>
                    </div>
                  </div>
                  <div className="filling-panel-block">
                    <p>Pencil color</p>
                    <div className="colors">
                      {colors.map((color, idx) => <div
                        key={idx}
                        onClick={() => this.instrumentColor(color, 'line')} 
                        style={{backgroundColor: color}}
                        className="color">
                          {this.props.state.globalColor === color ? <div></div> : ''}
                      </div>)}
                    </div>
                  </div>
                </div>}
              </div>
            </div>
          </div> : ''}
          {this.state.instruments.eraser.active ? <div className="instrument-preview">
            <h4>{this.state.instruments.eraser.en}</h4>
            <div className="block">
              <div className="addtional-blocks">
                <div className={`addtional-block ${instrumentStatement(15, 'eraser')}`} onClick={() => this.instrumentSize(15, 'eraser')}>
                  <img src={imgs.smallEraser} alt="eraser"/>
                </div>
                <div className={`addtional-block ${instrumentStatement(30, 'eraser')}`} onClick={() => this.instrumentSize(30, 'eraser')}>
                  <img src={imgs.mediumEraser} alt="eraser"/>
                </div>
                <div className={`addtional-block ${instrumentStatement(70, 'eraser')}`} onClick={() => this.instrumentSize(70, 'eraser')}>
                  <img src={imgs.hugeEraser} alt="eraser"/>
                </div>
              </div>
            </div>
          </div> : ''}
          {this.state.instruments.line.active ? <div className="instrument-preview">
            <h4>{this.state.instruments.line.en}</h4>
            <div className="block desktop-view">
              <div className="addtional-blocks">
                <div className={`addtional-block ${instrumentStatement(2, 'line')}`} onClick={() => this.instrumentSize(2, 'line')}>
                  <img src={imgs.smallLine} alt="curve" />
                </div>
                <div className={`addtional-block ${instrumentStatement(4, 'line')}`} onClick={() => this.instrumentSize(4, 'line')}>
                  <img src={imgs.mediumLine} alt="curve"/>
                </div>
                <div className={`addtional-block ${instrumentStatement(6, 'line')}`} onClick={() => this.instrumentSize(6, 'line')}>
                  <img src={imgs.hugeLine} alt="curve"/>
                </div>
                <div className={`addtional-block ${this.state.instruments.line.settings.dashedMode ? 'active' : ''}`} onClick={() => this.setDashedMode('line')}>
                  <img src={imgs.dashedLine} alt="curve"/>
                </div>
              </div>
                <div className="colors">
                  {colors.map((color, idx) => <div
                    key={idx}
                    onClick={() => this.instrumentColor(color, 'line')} 
                    style={{backgroundColor: color}}
                    className="color">
                      {this.props.state.globalColor === color ? <div></div> : ''}
                    </div>)}
                </div>
            </div>
            <div className="block phone-view">
              <div className="filling">
                <div className={`filling-instrumet ${this.state.toggleFillingPanel ? 'active' : ''}`} onClick={() => this.toggleFillingPanel()}>
                  {/* {fillColor.length ? <div
                    className={`color ${fillColor[0].includes('fff') ? 'add-border' : ''}`}
                    style={{backgroundColor: fillColor[0]}}>
                  </div> : <img src={imgs.filling} alt="filling"/>} */}
                  <img src={imgs.settings} alt="filling"/>
                </div>
                {this.state.toggleFillingPanel && <div className="filling-panel">
                <div className="filling-panel-block">
                    <p>Line size</p>
                    <div className="addtional-blocks">
                      <div className={`addtional-block ${instrumentStatement(2, 'line')}`} onClick={() => this.instrumentSize(2, 'line')}>
                        <img src={imgs.smallLine} alt="curve" />
                      </div>
                      <div className={`addtional-block ${instrumentStatement(4, 'line')}`} onClick={() => this.instrumentSize(4, 'line')}>
                        <img src={imgs.mediumLine} alt="curve"/>
                      </div>
                      <div className={`addtional-block ${instrumentStatement(6, 'line')}`} onClick={() => this.instrumentSize(6, 'line')}>
                        <img src={imgs.hugeLine} alt="curve"/>
                      </div>
                      <div className={`addtional-block ${this.state.instruments.line.settings.dashedMode ? 'active' : ''}`} onClick={() => this.setDashedMode('line')}>
                        <img src={imgs.dashedLine} alt="curve"/>
                      </div>
                    </div>
                  </div>
                  <div className="filling-panel-block">
                    <p>Line color</p>
                    <div className="colors">
                      {colors.map((color, idx) => <div
                        key={idx}
                        onClick={() => this.instrumentColor(color, 'line')} 
                        style={{backgroundColor: color}}
                        className="color">
                          {this.props.state.globalColor === color ? <div></div> : ''}
                      </div>)}
                    </div>
                  </div>
                </div>}
              </div>
            </div>
          </div> : ''}
          {this.state.instruments.figure.active ? <div className="instrument-preview">
            <h4>{this.state.instruments.figure.en}</h4>
            <div className="block desktop-view">
              <div className="addtional-blocks">
                {this.state.instruments.figure.types.titles.map((title, idx) => {
                    return <div key={idx} className={`addtional-block figures ${figureStatement(title)}`} onClick={() => this.setFigure(title)}>
                    <img src={imgs[title]} alt={title} />
                  </div>
                })}
                <div className={`addtional-block ${this.state.instruments.figure.settings.dashedMode ? 'active' : ''}`} onClick={() => this.setDashedMode('figure')}>
                  <img src={imgs.dashedCurve} alt="curve"/>
                </div>
              </div>
              <div className="colors">
                  {colors.map((color, idx) => <div
                    key={idx}
                    onClick={() => this.instrumentColor(color, 'figure')} 
                    style={{backgroundColor: color}}
                    className="color">
                      {this.props.state.globalColor === color ? <div></div> : ''}
                  </div>)}
              </div>
              <div className="filling">
                <div className={`filling-instrumet ${this.state.toggleFillingPanel ? 'active' : ''}`} onClick={() => this.toggleFillingPanel()}>
                  {fillColor.length ? <div
                    className={`color ${fillColor[0].includes('fff') ? 'add-border' : ''}`}
                    style={{backgroundColor: fillColor[0]}}>
                  </div> : <img src={imgs.filling} alt="filling"/>}
                </div>
                {this.state.toggleFillingPanel && <div className="filling-panel">
                  <p>Fill color</p>
                  <div className="filling-colors">
                    <div
                      onClick={() => this.figureFillingColor(null, 'figure')} 
                      className={`color ${!this.props.state.instruments.figure.settings.fillColor ? 'active' : ''}`}>
                        <img src={imgs.noneImg} alt="none"/>
                    </div>
                    {fillColors.map((color, idx) => <div
                      key={idx}
                      onClick={() => this.figureFillingColor(color, 'figure')} 
                      style={{backgroundColor: color}}
                      className="color">
                        {this.props.state.instruments.figure.settings.fillColor === color ? <div></div> : ''}
                    </div>)}
                  </div>
                </div>}
              </div>
            </div>
            <div className="block phone-view">
              <div className="filling">
                <div className={`filling-instrumet ${this.state.toggleFillingPanel ? 'active' : ''}`} onClick={() => this.toggleFillingPanel()}>
                  {/* {fillColor.length ? <div
                    className={`color ${fillColor[0].includes('fff') ? 'add-border' : ''}`}
                    style={{backgroundColor: fillColor[0]}}>
                  </div> : <img src={imgs.filling} alt="filling"/>} */}
                  <img src={imgs.settings} alt="filling"/>
                </div>
                {this.state.toggleFillingPanel && <div className="filling-panel">
                <div className="filling-panel-block">
                    <p>Figure</p>
                    <div className="addtional-blocks">
                      {this.state.instruments.figure.types.titles.map((title, idx) => {
                          return <div key={idx} className={`addtional-block figures ${figureStatement(title)}`} onClick={() => this.setFigure(title)}>
                          <img src={imgs[title]} alt={title} />
                        </div>
                      })}
                      <div className={`addtional-block ${this.state.instruments.figure.settings.dashedMode ? 'active' : ''}`} onClick={() => this.setDashedMode('figure')}>
                        <img src={imgs.dashedCurve} alt="curve"/>
                      </div>
                    </div>
                  </div>
                  <div className="filling-panel-block">
                    <p>Border color</p>
                    <div className="colors">
                      {colors.map((color, idx) => <div
                        key={idx}
                        onClick={() => this.instrumentColor(color, 'line')} 
                        style={{backgroundColor: color}}
                        className="color">
                          {this.props.state.globalColor === color ? <div></div> : ''}
                      </div>)}
                    </div>
                  </div>
                  <div className="filling-panel-block">
                    <p>Fill color</p>
                    <div className="filling-colors">
                      <div
                        onClick={() => this.figureFillingColor(null, 'figure')} 
                        className={`color ${!this.props.state.instruments.figure.settings.fillColor ? 'active' : ''}`}>
                          <img src={imgs.noneImg} alt="none"/>
                      </div>
                      {fillColors.map((color, idx) => <div
                        key={idx}
                        onClick={() => this.figureFillingColor(color, 'figure')} 
                        style={{backgroundColor: color}}
                        className="color">
                          {this.props.state.instruments.figure.settings.fillColor === color ? <div></div> : ''}
                      </div>)}
                    </div>
                  </div>
                </div>}
              </div>
            </div>
          </div> : ''}
          {this.state.instruments.text.active ? <div className="instrument-preview">
            <h4>{this.state.instruments.text.en}</h4>
            <div className="block">
            <div className="addtional-blocks">
                <div className={`addtional-block ${this.state.instruments.text.settings.boldMode ? 'active' : ''}`} onClick={() => this.setTextSettings('boldMode')}>
                  <span className="special-bold-item">B</span>
                </div>
                <div className={`addtional-block ${this.state.instruments.text.settings.italicMode ? 'active' : ''}`} onClick={() => this.setTextSettings('italicMode')}>
                  <span> <i className="special-italic-item">I</i> </span>
                </div>
                <input type="text" placeholder="Введите текст" onChange={e => this.setTextSettings('content', e.target.value)}/>
                <select onChange={e => {this.setTextSettings('size', e.target.value)}} defaultValue={16}>
                  {fontSizeNumbers.map((n, idx) => <option key={idx} value={n}>{n}</option>)}
                </select>
              </div>
            </div>
          </div> : ''}
          {this.state.instruments.glove.active ? <div className="instrument-preview">
            <h4>{this.state.instruments.glove.en}</h4>
            <div className="block">
            <div className="addtional-blocks">
              <div className={`addtional-block special-resize-block ${this.state.instruments.glove.settings.resize ? 'active' : ''}`} onClick={() => this.gloveResizMode(!this.state.instruments.glove.settings.resize)}>
                <img className="special-resize-block" src={imgs.resizeImg} alt="resizeImg" />
              </div>
              <div 
                className={`addtional-block special-resize-block`} 
                onClick={() => this.increaseRotationDegree(45)}>
                  <span className="rotation-degree special-resize-block">{this.props.state.rotationDegree}</span>
              </div>
            </div>
            </div>
          </div> : ''}
          {this.state.instruments.pointer.active ? <div className="instrument-preview">
            <h4>{this.state.instruments.pointer.en}</h4>
            <div className="block">
            <div className="addtional-blocks">
                <div className={`addtional-block ${this.state.instruments.pointer.settings.arrowRight ? 'active' : ''}`} onClick={() => this.setPointerSettings('arrowRight')}>
                  <img src={imgs.arrowRight} alt="arrowRight" />
                </div>
                <div className={`addtional-block ${this.state.instruments.pointer.settings.redPoint ? 'active' : ''}`} onClick={() => this.setPointerSettings('redPoint')}>
                <img src={imgs.redPoint} alt="redPoint" />
                </div>
              </div>
            </div>
          </div> : ''}
          {this.state.instruments.paper.active ? <div className="instrument-preview">
            <h4>{this.state.instruments.paper.en}</h4>
            <div className="block">
              <div className="addtional-blocks">
                <div className={`addtional-block ${instrumentStatement('17px', 'paper')}`} onClick={() => this.instrumentSize('17px', 'paper')}>
                  <span>1</span>
                </div>
                <div className={`addtional-block ${instrumentStatement('22px', 'paper')}`} onClick={() => this.instrumentSize('22px', 'paper')}>
                  <span>2</span>
                </div>
                <div className={`addtional-block ${instrumentStatement('31px', 'paper')}`} onClick={() => this.instrumentSize('31px', 'paper')}>
                  <span>3</span>
                </div>
                <div className={`addtional-block ${instrumentStatement('0', 'paper')}`} onClick={() => this.instrumentSize('0', 'paper')}>
                  <span>4</span>
                </div>
              </div>
            </div>
          </div> : ''}
          {this.state.dom.copyCancel ? <div className="instrument-preview" onClick={() => {this.props.copyCancel()}}>
            <button>Cancel copy</button>
          </div> : ''}
        </div>
        <div className="info">
          {this.state.dom.successCopy ? <div className="copy-success">
            <img src={imgs.copyIcon} alt="copy" />
            <span>Copied</span>
          </div> : ''}
          {this.state.dom.successImport ? <div className="message">
            <span>File import. Please wait..</span>
          </div> : ''}
          {this.state.dom.successExport ? <div className="message">
            <span>File export. Please wait..</span>
          </div> : ''}
          {this.state.dom.successPaste ? <div className="message">
            <span>Select the place and click</span>
          </div> : ''}
        </div>
      </header>
    );
  }
}

export default Header;