export function setDefaultActive(obj, type) {
  for (let key in obj) {
    if (type !== key)
      obj[key].active = false
  }

  return obj
}

export function saveImage(imagePath, imageName){
  const link = document.createElement('a')
  link.style.display = 'none'
  document.body.appendChild(link)
  link.setAttribute('download', imageName + '.png')
  link.setAttribute('href', imagePath.replace("image/png", "image/octet-stream"))
  link.click()
}

export function defaultCanvas() {
  let { innerWidth, innerHeight } = window,
      canvas = document.createElement('canvas')

  canvas.width = innerWidth
  canvas.height = innerHeight

  return canvas
}

export function generateCanvas(w, h) {
  let canvas = document.createElement('canvas')
  canvas.width = w; canvas.height = h

  return canvas
}

export function defaultPageHistory(){
  let { width, height } = defaultCanvas()
  return defaultCanvas().getContext('2d', { willReadFrequently: true }).getImageData(0, 0, width, height)
}

export function transformPagesToJson(state) {
  let { pages } = state
  let pagesCopy = pages.map((page) => {
    let history = page.history[page.history.length - 1]
    let { data, width, height, colorSpace } = history
    return Object.assign({}, {data: data.toString(), width, height, colorSpace})
  })

  return JSON.stringify(pagesCopy)
}

export function pageGenerator(n) {
  let pages = []
  for (let i = 0; i < n; i++) {
    let id = i + 1
    pages.push(
      Object.assign({}, {id, canvas: null, ctx: null, temp: null, history: [defaultPageHistory()]})
    )
  }

  return pages
}

export function transformJsonToPages(jsonStr) {
  let copy = JSON.parse(jsonStr)
  copy = copy.map((page, idx) => {
    let { data, width, height, colorSpace } = page
    let imageData = new ImageData( new Uint8ClampedArray(data.split(',')), width, height )
    return Object.assign(
      {
        id: idx + 1, canvas: null, ctx: null, 
        import: true,
        temp: imageData, 
        history: [imageData]
      },
      {data: data.toString(), width, height, colorSpace}
    )
  })
  return copy
}

export function downloadToFile (content, filename, contentType) {
  const a = document.createElement('a')
  const file = new Blob([content], {type: contentType})
  
  a.href= URL.createObjectURL(file)
  a.download = filename
  a.click()

  URL.revokeObjectURL(a.href)
}

export function downloadFile (file, fileName = 'data.json') {
  var a = document.createElement('a');
  a.href = URL.createObjectURL(file);
  a.download = fileName;
  document.body.appendChild(a);
  a.click();
  document.body.removeChild(a);
}