function checkFormat(json) {
  try {
    const requireFields = ['key', 'canvasId', 'base64']

    for (let i = 0; i < json.length; i++) {
      const element = json[i]
      let fields = [...requireFields]
      
      for (const key in element) {
        if (fields.includes(key)) {
          fields = fields.filter(f => f !== key)
        }
      }

      if (fields.length > 0) return false 
    }

    return true
  } catch (error) {
    console.log(error)
  }
}

export async function importRecordings(e, setRecords) {
  const { files } = e.target
  try {
    if (files && files.length) {

      const file = files[0]
      const reader = new FileReader()

      let types = ['application/json']
      if (file) {
        if (types.includes(file.type)) {
          reader.onload = async event => {
            try {
              let json = JSON.parse(event.target.result)

              if (json && json.length) {
                if (checkFormat(json)) {
                  setRecords([...json])
                } else
                  console.warn('Invalid format of JSON')
              }
            } catch (error) {
              console.log(error)
            }
          }
          
          reader.readAsText(files[0])
        } else {
          console.warn('Invalid file type')
        }
      }
    }

  } catch (e) {
    console.log(e)
  }
}