import { imgs } from "../../../../../common/imgs"
import useRecordingsList from "../../../../../hooks/voice-recorder/useRecordingsList"
import { moveDown, moveTop } from "../../../../../utils/records-moving"

export default function RecordingsList({ audio, canvasId, blob, methods, activeRecord, records: recordings }) {
  const { deleteAudio } = useRecordingsList(audio, blob, methods, recordings)

  const AudioFormat = ({ base64 }) => `data:audio/ogg;base64,${base64}`

  const AudioItem = record => {
    return record?.audio ? (
      <audio controls>
        <source src={record.audio} type="audio/mpeg"></source>
        <source src={record.audio} type="audio/ogg"></source>
        <source src={record.audio} type="audio/mp3"></source>
      </audio>  
    ) : (
      <audio controls>
        <source src={AudioFormat(record)} type="audio/mpeg"></source>
        <source src={AudioFormat(record)} type="audio/ogg"></source>
        <source src={AudioFormat(record)} type="audio/mp3"></source>
      </audio>  
    )
  }

  function liftUp(record, canvasId) {
    const { key } = record
    const recs = moveTop(recordings, key, canvasId)

    methods.recordsResave(recs)
  }

  function lowerDown(record, canvasId) {
    const { key } = record
    const recs = moveDown(recordings, key, canvasId)

    methods.recordsResave(recs)
  }

  return (
    <div className="recordings-container">
      {
        recordings.length > 0 ? (
          <div className={recordings.length > 5 ? 'recordings-list max' : 'recordings-list'}>
            {
              recordings.map((record) => {
                return canvasId === record.canvasId ?
                  <div onClick={e => methods.resaveActiveRecord(record.key)} className={`record ${activeRecord === record.key ? 'active' : ''}`} key={record.key}>
                    { recordings.reduce((accum, item) =>
                        accum += item.canvasId === record.canvasId ? 1 : 0, 0) > 1 }
                    {
                      recordings.reduce((accum, item) =>
                        accum += item.canvasId === record.canvasId ? 1 : 0, 0) > 1 ? (
                          <div className="moving">
                            <button onClick={() => liftUp(record, canvasId)} className="top moving-button">
                              <img src={imgs.arrowTop} alt="arrow" />
                            </button>

                            <button onClick={() => lowerDown(record, canvasId)} className="down moving-button">
                              <img src={imgs.arrowBottom} alt="arrow" />
                            </button>
                          </div>
                        ) : (
                          ''
                        )
                    }

                    { AudioItem(record) }                  

                    <button
                      className="delete-button control-button"
                      title="Delete this audio"
                      onClick={() => deleteAudio(record.key)}>
                      <img src={imgs.trashCan} alt="trashCan"/>
                    </button>
                  </div>
                  :
                  ''
              })
            }
          </div>
        ) : (
          ''
        )
      }

      {
        !recordings.some(record => record.canvasId === canvasId) ? (
          <div className="no-records">
            <span>You don't have any records</span>
          </div>
        ) : (
          ''
        )
      }
    </div>
  )
}