import { useState } from "react";
import { imgs } from "../../../../common/imgs";
import useRecorder from "../../../../hooks/voice-recorder/useRecorder"
import RecorderControls from "./components/RecorderControls";
import RecordingsList from "./components/RecordingsList";

export default function VoiceRecorder({ parentState, methods }) {
  const [activeMode, setActiveMode] = useState(false)
  const { recorderState, ...handlers } = useRecorder()
  const { audio, blob } = recorderState
  const { activeCanvasId } = parentState

  function toggleActiveMode(mode) {
    methods.resaveRecordsEdetingArea(mode)
    setActiveMode(mode)
  }

  return (
    <>
      <div className="voice-recording-block" title="Record or listen the voice">
        <button 
          className={
            activeMode ? 
              `voice-recording-button active ${recorderState.initRecording ? 'red-flag' : ''}` 
              : 
              `voice-recording-button ${recorderState.initRecording ? 'red-flag' : ''}`} 
          onClick={() => toggleActiveMode(!activeMode)}>
          <img src={imgs.voice} alt="voice"/>
          {
            recorderState.initRecording ? <div className="red-flag"></div> : ''
          }
        </button>
        <section className={activeMode ? 'voice-recorder active' : 'voice-recorder'}>
          <div className="recorder-container">
            <RecorderControls recorderState={recorderState} handlers={handlers} />
            <RecordingsList 
              canvasId={activeCanvasId} 
              audio={audio} 
              methods={methods}
              records={parentState.records}
              activeRecord={parentState.activeRecord}
              blob={blob} />
          </div>
        </section> 
      </div>
    </>
  ) 
}