import { imgs } from "../../../../../common/imgs";
import {formatMinutes, formatSeconds} from "../../../../../utils/time-formater"

export default function RecorderControls({ recorderState, handlers }) {
  const { recordingMinutes, recordingSeconds, initRecording } = recorderState
  const { startRecording, saveRecording, cancelRecording } = handlers

  return (
    <div className="controls-container">
      <div className="recorder-display">
        <div className="recording-time">
          {initRecording && <div className="recording-indicator"></div>}
          <span>{formatMinutes(recordingMinutes)}</span>
          <span>:</span>
          <span>{formatSeconds(recordingSeconds)}</span>
        </div>
      </div>
      <div className="controls-buttons">
        <div className="start-button-container">
          {initRecording ? (
            <button
              className="start-button control-button"
              title="Save recording"
              disabled={recordingSeconds === 0}
              onClick={saveRecording}
            >
              <img src={imgs.stop} alt="stop"/>
            </button>
          ) : (
            <button className="start-button control-button" title="Start recording" onClick={startRecording}>
              <img src={imgs.voice} alt="voice"/>
            </button>
          )}
        </div>
        {initRecording && (
          <div className="cancel-button-container">
            <button className="cancel-button control-button" title="Cancel recording" onClick={cancelRecording}>
              <img src={imgs.trashCan} alt="trashCan"/>
            </button>
          </div>
        )}
      </div>
    </div>
  );
}