import React from "react"
import { imgs } from "../../../common/imgs"

export class ScreenRecorder extends React.Component {
  constructor(props) {
    super(props)
    this.methods = this.props.methods

    this.state = {
      isActive: false,
      mediaRecorder: null,
      stream: null,
      audio: null,
    }
  }

  async recordScreen() {
    return await navigator.mediaDevices
      .getDisplayMedia({ video: true })
  }  
  
  async recordAudio() {
    return await navigator.mediaDevices.getUserMedia({
			audio: {
				echoCancellation: true,
				noiseSuppression: true,
				sampleRate: 44100
			}
		})
  }

  createRecorder (stream, audio) {
    const mixedStream = new MediaStream([...stream.getTracks(), ...audio.getTracks()])
  
    const mediaRecorder = new MediaRecorder(mixedStream)

    this.setStreamMethods(mediaRecorder) 
  
    return mediaRecorder
  }

  setStreamMethods(mediaRecorder) {
    // The stream data is stored in this array
    let recordedChunks = []
    let mimeType = 'video/mp4'

    mediaRecorder.onstop = () =>  {
      this.saveFile(recordedChunks, mimeType)
      recordedChunks = []
   }

   mediaRecorder.ondataavailable = (e) => {
      if (e.data.size > 0) {
        recordedChunks.push(e.data)
      }   
    }
    
    // For every 200ms the stream data will be stored in a separate chunk.
    mediaRecorder.start(200)
  }

  saveFile(recordedChunks){
    const blob = new Blob(recordedChunks, { type: 'video/webm' })

     let filename = window.prompt('Enter file name'),
         downloadLink = document.createElement('a')

     downloadLink.href = URL.createObjectURL(blob)
     downloadLink.download = `${filename}.webm`
 
     document.body.appendChild(downloadLink)
     downloadLink.click()
     URL.revokeObjectURL(blob) // Clear from memory
     document.body.removeChild(downloadLink)
  }

  async startRecorder() {
    let stream = await this.recordScreen()
    let audio = await this.recordAudio()

    const mediaRecorder = this.createRecorder(stream, audio)

    this.setState({ mediaRecorder, stream, audio })
  }

  finishRecorder() {
    const { mediaRecorder, stream, audio } = this.state
    
    if (mediaRecorder) {
      mediaRecorder.stop()
      stream.getTracks().forEach((track) => track.stop())
	    audio.getTracks().forEach((track) => track.stop())
    }
    
    this.setState({ mediaRecorder: null, stream: null, audio: null })
  }

  getPause() {
    const { mediaRecorder } = this.state
    if (mediaRecorder.state === 'recording') mediaRecorder.pause()
  }

  resumeRecorder() {
    const { mediaRecorder } = this.state
    if (mediaRecorder.state === 'paused') mediaRecorder.resume()
  }

  render() {
    return <div 
      className="possibilitie screen-recorder" 
      style={{ background: this.state.isActive ? '#ccc' : 'none' }}
      onClick={() => this.setState({ isActive: !this.state.isActive })}>
        
      <img src={imgs.screenRecorder} alt="screenRecorder" />
      
      {
        this.state.mediaRecorder ? (
          <div className={
            this.state.mediaRecorder.state === 'paused' ? 'red-flag stop' : 'red-flag'
          }></div>
        ) : ''
      }

      {
        this.state.isActive ? (
          <section className="screen-recorder-tools">
            {
              this.state.mediaRecorder ? (
                <>
                  { this.state.mediaRecorder.state === 'recording' ? (
                    <button onClick={() => this.getPause()} title="Pause the record">
                      <img src={imgs.pause} alt="pause" />
                      <span>Pause</span>
                    </button>
                  ) : ''  }

                  { this.state.mediaRecorder.state === 'paused' ? (
                      <button onClick={() => this.resumeRecorder()} title="Continue record">
                        <img src={imgs.record} alt="start" />
                        <span>Continue</span>
                      </button>
                    ) : ''  }
                </>
              ) : (
                <button onClick={() => this.startRecorder()} title="Start recorder">
                  <img src={imgs.record} alt="record" />
                  <span>Start</span>
                </button>
              )
            }
            {
              this.state.mediaRecorder ? (
                <button onClick={() => this.finishRecorder()} title="Finish recorder">
                  <img src={imgs.stop} alt="stop" />
                  <span>Finish</span>
                </button>
              ) : (
                ''
              )
            }
          </section>
        ) : (
          ''
        )
      }
  </div>
  }
}